


















import car from 'static/img/car.png'
import broken from 'static/img/broken.png'
import {getCalllMsg, bindPhone} from 'common/js/requestUrl'
import {request} from 'common/js/common'

export default {
	data() {
		return {
			car,
			broken,
			userId: false,
			card: '免打扰中',
		};
	},
	onLoad(query){
		const scene = decodeURIComponent(query.scene)
		
		if(scene !='undefined'){
			this.userId = scene
		}
		this.getCalllMsg()
	},
	methods: {
		getCalllMsg(res){
			request(getCalllMsg,{
				user_id : this.userId
			},(res) => {
				this.card = res.result.prefix + res.result.card
			})
		},
		bindPhone(){
			request(bindPhone,{
				user_id : this.userId
			},(res) => {
				uni.makePhoneCall({
					phoneNumber: res.result //仅为示例
				});
			})
		}
	}
}
